<template>
  <div id="chat">
    <chat-header />
    <chat-main :message="message" :local="local" />
    <chat-footer @submit="provideData" />
  </div>
</template>

<script>
import ChatFooter from "./chat-room/ChatFooter.vue";
import ChatHeader from "./chat-room/ChatHeader.vue";
import ChatMain from "./chat-room/ChatMain.vue";
export default {
  components: { ChatMain, ChatFooter, ChatHeader },
  data() {
    return {
      message: "",
      local: false,
    };
  },
  methods: {
    provideData({ msg, local }) {
      this.message = msg;
      this.local = local;
    },
  },
};
</script>

<style scoped>
#chat {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  overflow: hidden;
}
</style>
