<template>
  <div id="chatfooter">
    <div class="input-message">
      <el-form
        :inline="true"
        :model="formInline"
        class="demo-form-inline aa"
        @submit.native.prevent="onSubmit"
      >
        <el-form-item>
          <el-input
            v-model="formInline.text"
            placeholder="Enter message"
          ></el-input>
        </el-form-item>
        <el-form-item>
          <el-button
            type="primary"
            @click="onSubmit"
            :round="this.round"
            icon="el-icon-right"
          ></el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  name: "ChatFooter",
  data() {
    return {
      formInline: {
        text: "",
      },
      round: true,
      local: true,
    };
  },
  methods: {
    onSubmit() {
      this.$emit("submit", { msg: this.formInline.text, local: this.local });
      this.$socket.client.emit("chat message", this.formInline.text);
      this.formInline.text = "";
    },
  },
};
</script>

<style scoped>
#chatfooter {
  width: 100%;
}
.el-form {
  display: flex;
  justify-content: space-between;
  flex-basis: auto;
}
.el-form-item:first-child {
  width: 90%;
}
.el-form-item:last-child {
  width: 10%;
}
.aa >>> .el-form-item__content {
  width: 100% !important;
}
</style>
