<template>
  <div id="chatmain">
    <div>
      <div class="chatBox" v-for="(item, index) in messages" :key="index">
        <local-message :message="item.msg" v-if="item.local" />
        <external-message :message="item.msg" v-else />
      </div>
    </div>
  </div>
</template>

<script>
import ExternalMessage from "./chat-room-components/externalMessage.vue";
import LocalMessage from "./chat-room-components/localMessage.vue";
export default {
  name: "ChatMain",
  components: {
    LocalMessage,
    ExternalMessage,
  },
  props: {
    message: String,
    local: Boolean,
  },
  data() {
    return {
      messages: [],
    };
  },
  methods: {
    updateMessages() {
      if (this.message === 0 || !this.message.trim()) return;
      this.messages.push({
        msg: this.message,
        local: this.local,
      });
    },
  },
  watch: {
    message() {
      return this.updateMessages();
    },
  },
  created() {
    this.$socket.$subscribe("newMessage", (data) => {
      this.messages.push({
        msg: data.msg,
        local: false,
      });
    });
    this.$socket.$subscribe("newUserJoined", (data) => {
      console.log(data);
      this.messages.push({
        msg: "connection message: " + data.msg,
        local: false,
      });
    });
    this.$socket.$subscribe("userLeft", (data) => {
      console.log(data);
      this.messages.push({
        msg: "connection message: " + data,
        local: false,
      });
    });
  },
};
</script>

<style scoped>
#chatmain {
  display: flex;
  flex-direction: column;
  height: 90%;
  overflow-y: auto;
  padding: 1rem;
}
.chatBox:first-child {
  margin-top: auto !important;
}
</style>
